<template>
    <div :style="{
          display: 'inline-block',
          width: '100%',
        }">
        <a-calendar v-model="value" />
    </div>
</template>

<script>
    import moment from 'moment';

    export default {
        data() {
            return {
                value: moment(null),
            };
        },
        methods: {
            onSelect(value) {
                this.value = value;
            },
            onPanelChange(value) {
                this.value = value;
            },
        },
        created: function () {
            var date = new Date();
            console.log(date)
            date =
                date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
            this.value = moment(date);
        }
    };
</script>